import Layout from '../components/Layout'
const img = {
    width: '75vw',
    margin: '4vh auto'
}
const wrapper = {
    display: 'flex',
    flexDirection: 'column'
}

const text = {
    width: '75vw',
    margin: '0 auto 30px auto'
}
const Who = () => (
    <Layout solid>
    <div style={wrapper}>
    <img style={img}
    src="https://sun9-48.userapi.com/c857436/v857436377/1bd0ce/Pjx8kt49udM.jpg"
    alt="me"
    ></img>
    <p style={text}>
Доброго дня!
Я - Дарья, художница эпоксидной смолой и арт-блогер.
За 1,5 года работы со смолой, мне удалось:
</p>
<ul>
<li> обучить рисованию более 80 человек</li>
<li> провести 25 мастер-классов</li>
<li> выполнить больше 100 заказов на декор</li>
</ul>
<p>
Моя цель - помочь креативным людям развивать своё творчество и
показать, что в нём нет места страху.

Вдохновляйтесь, творите и показывайте своё творчество миру вместе со мной!
    </p>
    </div>
    </Layout>
)
export default Who;